import { comp, drop, iterator1, map, multiplex } from "@thi.ng/transducers";
import { momentum } from "./momentum";
import { sma } from "./sma";
export function rsi(period, src) {
    return src
        ? iterator1(rsi(period), src)
        : comp(momentum(1), multiplex(comp(map((x) => (x > 0 ? x : 0)), sma(period)), comp(map((x) => (x < 0 ? -x : 0)), sma(period))), drop(period - 1), map((hl) => 100 - 100 / (1 + hl[0] / Math.max(1e-6, hl[1]))));
}
