import { Context } from '../api';
import { resource } from '../utils';
import * as routes from "../routes";
import { routeLink } from './route-link';
import { workSpec } from './work-spec';

const galleryImage = ({ ui, views }: Context) => {
  const pid = views.project.deref();
  const project = views.projects.deref()[pid];
  if (project) {
    const index = views.galleryImage.deref();
    const images = project['images'];
    const route = [routes.WORK_GALLERY.id, {
      id: pid, index: (index + 1) % images.length
    }];
    return ['div', ui.gallery,
      [routeLink, {}, route,
        ['img', { ...ui.galleryImg, src: resource(images[index]) }]
      ],
      ['div', ui.galleryInfo,
        [workSpec, project]
      ]
    ];
  }
}

export function gallery({ ui }: Context) {
  return () => ['div', ui.home, [galleryImage]]
}