import { $iter, comp, drop, map, multiplex, partition } from "@thi.ng/transducers";
import { mse } from "./mse";
import { sma } from "./sma";
export function sd(...args) {
    const iter = $iter(sd, args);
    if (iter) {
        return iter;
    }
    const period = args[0] || 20;
    const scale = args[1] || 1;
    return comp(multiplex(partition(period, 1), sma(period)), drop(period - 1), map(([window, mean]) => Math.sqrt(mse(window, mean) / period) * scale));
}
