import { appLink } from '@thi.ng/hdom-components';
import { EV_TOGGLE_VALUE } from '@thi.ng/interceptors';
import { map } from '@thi.ng/transducers';

import { Context } from '../api';
import * as ev from "../events";
import { routeLink } from './route-link';

export const navSwitch = ({ ui, bus }: Context) =>
  [appLink, ui.nav.switch,
    () => bus.dispatch([EV_TOGGLE_VALUE, 'nav.visible']),
    ['span', ui.icons.menu]];

const navItemGroup = ({ ui, bus }: Context, item) =>
  ['li', ui.nav.item,
    ['div',
      [appLink, item.active ? ui.nav.linkActive : ui.nav.link,
        () => bus.dispatch([ev.TOGGLE_NAV_GROUP, item.name]), item.name],
      item.open ? ['ul', ui.nav.subContent, ...map(item => [navItem, item], item.children)] : null
    ]
  ];

const navItem = ({ ui }: Context, item) =>
  ['li', ui.nav.item,
    [routeLink, item.active ? ui.nav.linkActive : ui.nav.link,
      item.route, item.name]];

const navContent = ({ ui, views }: Context) =>
  ['ul', ui.nav.content,
    ...map(
      item => [item.hasOwnProperty('children') ? navItemGroup : navItem, item],
      views.nav.deref().items)
  ];

export const nav = ({ ui, views }: Context) => {
  const show = views.isLarge.deref() || views.nav.deref().visible;
  return show ? ['div', ui.nav.container, navContent] : null;
}