import { comp, drop, iterator1, map, multiplex } from "@thi.ng/transducers";
import { wma } from "./wma";
export function hma(period, src) {
    return src
        ? iterator1(hma(period), src)
        : comp(multiplex(wma((period / 2) | 0), wma(period)), drop(period - 1), map((w) => 2 * w[0] - w[1]), wma(Math.sqrt(period)));
}
