import {
  FX_STATE,
  valueSetter,
  trace
} from '@thi.ng/interceptors';
import { filter, comp, map, groupByObj, compR, push, transduce, assocObj } from '@thi.ng/transducers';
import { setIn, updateIn, getIn } from '@thi.ng/paths';
import { EVENT_ROUTE_CHANGED, Route } from "@thi.ng/router";

import { Config, NavItem, NavItemGroup } from "./api";
import * as routes from "./routes";
import * as fx from "./effects";
import * as ev from "./events";
import { home } from "./components/home";
import { contact } from './components/contact';
import { join } from './components/join';
import { workList } from './components/work-list';
import { gallery } from './components/gallery';
import { jsonReader, resourceFetcher } from './utils';

const WORK_ITEMS = [
  {
    type: 'all',
    name: 'All'
  },
  {
    type: 'shop',
    name: 'Shop'
  },
  {
    type: 'hotel',
    name: 'Hotel'
  }
];

const NAV_ITEMS: Partial<NavItem | NavItemGroup>[] = [
  {
    name: 'Works',
    children: [...map(item => ({
      name: item.name,
      route: [routes.WORKS_LIST.id, { type: item.type }],
      match: ({ id, params }: Route) => id == routes.WORKS_LIST.id && params.type == item.type
    }), WORK_ITEMS)]
  },
  {
    name: 'Contact',
    route: [routes.CONTACT.id, null],
    match: ({ id }: Route) => id == routes.CONTACT.id,
  },
  {
    name: 'Join',
    route: [routes.JOIN.id, null],
    match: ({ id }: Route) => id == routes.JOIN.id,
  }
];

const isNavItemGroup = (group: Partial<NavItem | NavItemGroup>): group is NavItemGroup =>
  group.hasOwnProperty('children');

const getNavItems = (route: Route) =>
  [...map(item => {
    item['active'] = false;
    if (isNavItemGroup(item)) {
      let matched = false;
      for (let i = 0; i < item['children'].length; i++) {
        const child = item['children'][i];
        child.active = child.match(route);
        if (!matched) {
          matched = child.active;
        }
      }
      item['open'] = item['active'] = matched;
    } else {
      item['active'] = item['match'](route)
    }
    return item;
  }, NAV_ITEMS)];

const toggleGroup = name => items => [...map(item => {
  if (item['name'] == name) {
    item['open'] = !item['open']
  }
  return item;
}, items)];

const parseProjects = projects => [...map(proj => {
  proj['date'] = new Date(Date.parse(proj['date']));
  proj['construction_date'] = new Date(Date.parse(proj['construction_date']));
  return proj
}, projects)];

export const CONFIG: Config = {
  router: {
    useFragment: true,
    defaultRouteID: routes.HOME.id,
    routes: [
      routes.HOME,
      routes.WORKS_LIST,
      routes.WORK_DETAIL,
      routes.WORK_GALLERY,
      routes.CONTACT,
      routes.JOIN,
    ]
  },

  events: {
    [EVENT_ROUTE_CHANGED]: [
      valueSetter("route"),
      (state, [_, route]) => {
        const state1 = setIn(state, 'nav.items', getNavItems(route));
        return { [FX_STATE]: setIn(state1, 'nav.visible', false) };
      },
      (state, [_, route]) => {
        let state1;
        if (route.id == routes.WORK_DETAIL.id) {
          state1 = setIn(state, 'project', route.params.id);
        } else if (route.id == routes.WORK_GALLERY.id) {
          const s = setIn(state, 'project', route.params.id);
          state1 = setIn(s, 'galleryImage', parseInt(route.params.index));
        }
        return { [FX_STATE]: state1 };
      }
    ],
    [ev.ROUTE_TO]: (_, [__, route]) => ({ [fx.ROUTE_TO]: route }),

    [ev.TOGGLE_NAV_GROUP]:
      (state, [_, name]) => ({
        [FX_STATE]: updateIn(state, 'nav.items', toggleGroup(name))
      }),

    [ev.RESIZE]: valueSetter('isLarge'),

    [ev.LOAD_PROJECTS]: [
      resourceFetcher('/projects/index.html', ev.PARSE_PROJECTS, ev.LOAD_PROJECTS_ERROR),
    ],
    [ev.PARSE_PROJECTS]: jsonReader(db => [ev.RECEIVE_PROJECTS, parseProjects(db.projects)]),
    [ev.RECEIVE_PROJECTS]: (state, [_, projects]) => {
      const projs = transduce(
        map(proj => [proj['id'], proj]),
        assocObj(),
        projects
      );
      const state1 = setIn(state, 'projects', projs);

      const groups = groupByObj({
        key: p => p['category'],
        group: compR(push(), (acc, x) => (acc.push(x['id']), acc))
      }, projects);
      const state2 = setIn(state1, 'projectGroups', groups);

      const featured = transduce(
        comp(
          filter(p => p['featured']),
          map(p => p['id'])
        ),
        push(),
        projects
      );
      const state3 = setIn(state2, 'featured', featured);
      return { [FX_STATE]: setIn(state3, 'initialized', true) };
    },
    [ev.LOAD_PROJECTS_ERROR]: trace,

    [ev.NEXT_FEATURED_PROJECT]: (state, _) => {
      const featured = getIn(state, 'featured');
      return {
        [FX_STATE]: updateIn(state, 'currentFeatured',
          v => featured.length > 0 ? (v + 1) % featured.length : 0)
      }
    }
  },

  effects: {},

  components: {
    [routes.HOME.id]: home,
    [routes.WORKS_LIST.id]: workList,
    [routes.WORK_DETAIL.id]: workList,
    [routes.WORK_GALLERY.id]: gallery,
    [routes.CONTACT.id]: contact,
    [routes.JOIN.id]: join,
  },

  domRoot: "app",

  initialState: {
    initialized: false,
    nav: {
      visible: true,
      items: NAV_ITEMS
    },
    isLarge: false,
    project: '',
    projects: {},
    projectGroups: {},

    currentFeatured: 0,
    featured: [],
  },

  views: {
    initialized: 'initialized',
    nav: 'nav',
    isLarge: 'isLarge',
    project: 'project',
    projects: 'projects',
    projectGroups: 'projectGroups',

    currentFeatured: 'currentFeatured',
    featured: 'featured',

    galleryImage: 'galleryImage'
  },

  ui: {
    app: { class: 'sans-serif f6 relative dark-gray lh-title h-100' },
    link: { class: 'link' },
    icons: {
      menu: { class: 'menu icon' },
    },

    root: { class: 'dg grid h-100' },

    header: { class: 'pa3 header relative' },
    logo: { class: 'f3 absolute v-center link' },

    nav: {
      container: { class: 'nav relative' },
      switch: { class: 'right-1 link absolute f5 dn-ns v-center' },
      content: { class: 'ph3 ma0 w3 list pa0' },
      item: { class: 'mv1' },
      link: { class: 'link hover-b' },
      linkActive: { class: 'b link' },
      subContent: { class: 'ml3 list pa0' },
    },

    home: { class: 'content relative' },
    gallery: { class: '' },
    homeGalleryImg: { class: 'absolute cover vh-100 top-0 left-0 fade-in-out' },
    galleryImg: { class: 'absolute cover vh-100 top-0 left-0' },

    galleryInfo: { class: 'absolute bottom-1 right-2 mw-70' },

    workList: { class: 'content grid dg h-100 w-100 absolute' },
    workListThumb: { class: 'content-left pl2 mt1-ns' },
    workDetail: { class: 'content-right overflow-scroll' },

    project: {
      group: { class: 'mb2' },
      category: { class: 'mb1' },
      thumbList: { class: 'list dg thumb-grid ma0 pa0' },
      thumbItem: { class: 'w3 h3' },
      thumbContainer: { class: 'relative thumb-container' },
      thumbTitle: { class: 'no-underline absolute vh-center tc dn thumb-title white nus' },
      thumbImg: { class: 'db thumb-img' },

      info: { class: '' },
      title: { class: 'f5 mb5-ns mb3 mt0 mt1-ns' },
      spec: { class: 'dg project-spec f7 mb3' },
      image: { class: 'db img mb1' }
    },

    contact: { class: 'content grid dg' },
    contentOne: { class: 'content-one pa3 pa0-ns pl2-ns mt1-ns' },
    join: { class: 'content grid dg' },

    title: { class: 'f5 mb3 b' },
    specform: { class: 'dg spec-grid' }
  }
};