import { $iter, compR, step } from "@thi.ng/transducers";
import { ema } from "./ema";
export function macd(...args) {
    return ($iter(macd, args) ||
        ((rfn) => {
            const reduce = rfn[2];
            const maFast = step(ema(args[0] || 12));
            const maSlow = step(ema(args[1] || 26));
            const maSmooth = step(ema(args[2] || 9));
            return compR(rfn, (acc, x) => {
                const fast = maFast(x);
                const slow = maSlow(x);
                if (slow == null)
                    return acc;
                const macd = fast - slow;
                const signal = maSmooth(macd);
                if (signal == null)
                    return acc;
                return reduce(acc, {
                    macd,
                    signal,
                    div: macd - signal,
                    fast,
                    slow
                });
            });
        }));
}
