import { FX_DISPATCH_ASYNC, FX_FETCH, } from '@thi.ng/interceptors';

export const resource = path => `https://iwongharvey.github.io/alternability${path}`;

export const readJson = (res: Response, cb) => {
  const reader = res.body.getReader();
  const decoder = new TextDecoder();
  let data = '';
  const process = ({ done, value }) => {
    if (done) {
      cb(JSON.parse(data));
    } else {
      data += decoder.decode(value);
      return reader.read().then(process);
    }
  };
  reader.read().then(process);
}

export const resourceFetcher = (path, success, error) => () => ({
  [FX_DISPATCH_ASYNC]: [FX_FETCH, resource(path), success, error]
});

export const jsonReader = cb => (state, [_, res], bus) => {
  readJson(res, db => bus.dispatch(cb(db)));
  return state;
}