import { Context } from '../api';
import * as routes from "../routes";
import { routeLink } from './route-link';
import { nav, navSwitch } from './nav';

const logo = ({ ui }: Context) =>
  [routeLink, ui.logo, [routes.HOME.id, null], 'Alternability'];

const header = ({ ui }: Context) =>
  ['div', ui.header, logo, navSwitch];

export function root({ ui, views }: Context) {
  return ['div', ui.root,
    views.routeComponent,
    header,
    nav,
  ];
}