import { DCons } from "@thi.ng/dcons";
import { illegalArgs } from "@thi.ng/errors";
import { compR, iterator1 } from "@thi.ng/transducers";
export function momentum(period, src) {
    if (src) {
        return iterator1(momentum(period), src);
    }
    period |= 0;
    period < 1 && illegalArgs("period must be >= 1");
    return (rfn) => {
        const reduce = rfn[2];
        const window = new DCons();
        return compR(rfn, (acc, x) => {
            window.push(x);
            if (window.length <= period) {
                return acc;
            }
            const prev = window.drop();
            return reduce(acc, x - prev);
        });
    };
}
