import {
  map, mapIndexed, pairs,
  // transduce, comp, filter, push
} from '@thi.ng/transducers';
import { routeLink } from './route-link';

import { Context, Project } from '../api';
import { resource } from '../utils';
import * as routes from "../routes";
import { workSpec } from './work-spec';

const projectThumb = ({ ui }: Context, project) => {
  const route = [routes.WORK_DETAIL.id, { id: project.id }];
  return ['div', ui.project.thumbContainer,
    [routeLink, {}, route,
      ['img', { ...ui.project.thumbImg, src: resource(project.thumbnail) }]
    ],
    [routeLink, ui.project.thumbTitle, route, project.title],
  ];
}

const projectGroup = ({ ui, views }: Context, name, pids) => {
  const projects = views.projects.deref();
  return ['div', ui.project.group,
    ['div', ui.project.category, name],
    ['ul', ui.project.thumbList,
      ...map((pid: string) => ['li', ui.project.thumbItem, [projectThumb, projects[pid]]], pids)
    ]
  ]
}

const projectImage = ({ ui }: Context, project, index) => {
  const route = [routes.WORK_GALLERY.id, { id: project.id, index }];
  const url = project.images[index];
  return [routeLink, {}, route, ['img', { ...ui.project.image, src: resource(url) }]];
}

const projectInfo = ({ ui }: Context, project: Project) => {
  return ['div', ui.project.info,
    [workSpec, project],
    ['div',
      ...mapIndexed((i, _) => [projectImage, project, i], project.images)]
  ];
}

const projectDetail = ({ ui, views }: Context, pid: string) => {
  const project = views.projects.deref()[pid];
  return project ? ['div', ui.workDetail, [projectInfo, project]] : null;
}

export function workList({ ui, views }: Context) {
  return () => {
    return ['div', ui.workList,
      ['div', ui.workListThumb,
        ...map(([name, pids]) => [projectGroup, name, pids],
          pairs(views.projectGroups.deref()))
      ],

      [projectDetail, views.project.deref()]
    ];
  }
}