import { isNumber } from "@thi.ng/checks";
import { comp, iterator1, map, partition, range } from "@thi.ng/transducers";
import { dot } from "./dot";
export function wma(weights, src) {
    if (src) {
        return iterator1(wma(weights), src);
    }
    let period, wsum;
    if (isNumber(weights)) {
        period = weights | 0;
        weights = [...range(1, period + 1)];
        wsum = (period * (period + 1)) / 2;
    }
    else {
        period = weights.length;
        wsum = weights.reduce((acc, x) => acc + x, 0);
    }
    return comp(partition(period, 1), map((window) => dot(window, weights) / wsum));
}
