import { Context, Project } from '../api';

export const workSpec = ({ ui }: Context, project: Project) =>
  ['div',
    ['div', ui.project.title, project.title],

    ['div', ui.project.spec,
      'Client:', project.client,
      'Area:', project.area,
      'Category:', project.category,
      'Designer:', project.designer,
      'Members:', project.members,
      'Construction:', project.construction,
      'Constructor:', project.constructor,
      'Design Date:', `${project.date.getFullYear()}-${project.date.getMonth()}`,
      'Construction Date:', `${project.date.getFullYear()}-${project.date.getMonth()}`,
      'Location:', project.location,
    ],
  ]