import { compare as cmp } from "@thi.ng/compare";
import { identity } from "@thi.ng/compose";
import { comp } from "../func/comp";
import { $iter, iterator } from "../iterator";
import { mapcat } from "./mapcat";
import { partition } from "./partition";
export function partitionSort(...args) {
    const iter = $iter(partitionSort, args, iterator);
    if (iter) {
        return iter;
    }
    const { key, compare } = Object.assign({ key: identity, compare: cmp }, args[1]);
    return comp(partition(args[0], true), mapcat((window) => window.slice().sort((a, b) => compare(key(a), key(b)))));
}
