import { isString } from "@thi.ng/checks";
export const link = (attribs, body) => [
    "a",
    isString(attribs) ? { href: attribs } : attribs,
    body
];
export const appLink = (_, attribs, onclick, body) => [
    "a",
    Object.assign({ href: "#", onclick: (e) => {
            e.preventDefault();
            onclick(e);
        } }, attribs),
    body
];
