import { $iter, comp, drop, map, multiplex, partition } from "@thi.ng/transducers";
import { mse } from "./mse";
import { sma } from "./sma";
export function bollinger(...args) {
    const iter = $iter(bollinger, args);
    if (iter) {
        return iter;
    }
    const period = args[0] || 20;
    const sd = args[1] || 2;
    return comp(multiplex(partition(period, 1), sma(period)), drop(period - 1), map(([window, mean]) => {
        const std = Math.sqrt(mse(window, mean) / period) * sd;
        const min = mean - std;
        const max = mean + std;
        const pb = (window[period - 1] - min) / (max - min);
        return { min, max, mean, pb };
    }));
}
