import { illegalArgs } from "@thi.ng/errors";
import { compR, iterator1 } from "@thi.ng/transducers";
export function ema(period, src) {
    if (src) {
        return iterator1(ema(period), src);
    }
    period |= 0;
    period < 2 && illegalArgs("period must be >= 2");
    const k = 2 / (period + 1);
    return (rfn) => {
        const reduce = rfn[2];
        let window = [];
        let sum = 0;
        let ema;
        return compR(rfn, (acc, x) => {
            if (ema != null) {
                ema += (x - ema) * k;
                return rfn[2](acc, ema);
            }
            else {
                window.push(x);
                sum += x;
                if (window.length == period) {
                    ema = sum / period;
                    window = null;
                    return reduce(acc, ema);
                }
                return acc;
            }
        });
    };
}
