import { Context } from '../api';

export function contact({ ui }: Context) {
  return () => ['div', ui.contact,
    ['div', ui.contentOne,
      ['div', ui.title, 'Contact Us'],
      ['div', ui.specform,
        'Tel:', '(+86) 135 3399 1206',
        'Email:', ['a', { href: 'mailto:info@i-ana.com' }, 'info@i-ana.com'],
        'Addr:', `3A01, Block 1-2, Sihai City, No. 390, East Hanxi Avenue, Panyu District Guangzhou`
      ]
    ]
  ];
}