import { iterator } from "../iterator";
import { isReduced } from "../reduced";
export function takeLast(n, src) {
    return src
        ? iterator(takeLast(n), src)
        : ([init, complete, reduce]) => {
            const buf = [];
            return [
                init,
                (acc) => {
                    while (buf.length && !isReduced(acc)) {
                        acc = reduce(acc, buf.shift());
                    }
                    return complete(acc);
                },
                (acc, x) => {
                    if (buf.length === n) {
                        buf.shift();
                    }
                    buf.push(x);
                    return acc;
                }
            ];
        };
}
