import { Context } from '../api';
import * as ev from "../events";
import * as routes from "../routes";
import { routeLink } from './route-link';
import { resource } from '../utils';

const featuredProjects = ({ ui, views, bus }: Context) => {
  bus.dispatchLater([ev.NEXT_FEATURED_PROJECT], 4000);
  const pid = views.featured.deref()[views.currentFeatured.deref()];
  const projects = views.projects.deref();
  return ['div', ui.gallery,
    [routeLink, {}, [routes.WORK_DETAIL.id, { id: pid }],
      ['img', { ...ui.homeGalleryImg, src: resource(projects[pid]['images'][0]) }]
    ]
  ];
}

export function home({ ui, views }: Context) {
  return () => ['div', ui.home,
    views.featured.deref().length > 0 ? [featuredProjects] : null
  ];
}