import { IObjectOf } from "@thi.ng/api";
import { fromEvent } from '@thi.ng/rstream';
import { Atom } from "@thi.ng/atom";
import { isArray } from "@thi.ng/checks";
import { start } from "@thi.ng/hdom";
import { EventBus } from "@thi.ng/interceptors";
import { EVENT_ROUTE_CHANGED, HTMLRouter } from "@thi.ng/router";

import { Config, Context, AppViews, Views, ViewSpec } from "./api";
import * as fx from "./effects";
import * as ev from "./events";
import { root } from './components/root';

export class App {
  config: Config;
  ctx: Context;
  state: Atom<any>;
  router: HTMLRouter;

  constructor(config: Config) {
    this.config = config;
    this.state = new Atom(config.initialState || {});
    this.ctx = {
      bus: new EventBus(this.state, config.events, config.effects),
      views: <AppViews<Views>>{},
      ui: config.ui
    };
    this.addViews(this.config.views);
    this.router = new HTMLRouter(config.router);
    // connect router to event bus so that routing events are processed
    // as part of the normal batched event processing loop
    this.router.addListener(EVENT_ROUTE_CHANGED, e =>
      this.ctx.bus.dispatch([EVENT_ROUTE_CHANGED, e.value])
    );
    this.ctx.bus.addEffect(fx.ROUTE_TO, ([id, params]) =>
      this.router.routeTo(this.router.format(id, params))
    );

    this.addViews({
      route: "route",
      routeComponent: [
        "route.id",
        (id) =>
          (this.config.components[id] ||
            (() => ["div", `missing component for route: ${id}`]))(
              this.ctx
            )
      ]
    });

  }

  addViews(specs: IObjectOf<ViewSpec>) {
    const views = this.ctx.views;
    for (let id in specs) {
      const spec = specs[id];
      if (isArray(spec)) {
        views[id] = this.state.addView(spec[0], spec[1]);
      } else {
        views[id] = this.state.addView(spec);
      }
    }
  }

  start() {
    this.router.start();
    const { bus } = this.ctx;
    fromEvent(window, 'resize').subscribe({
      next() {
        bus.dispatch([ev.RESIZE, window.matchMedia('(min-width: 30em)').matches]);
      }
    });
    bus.dispatch([ev.RESIZE, window.matchMedia('(min-width: 30em)').matches]);
    bus.dispatch([ev.LOAD_PROJECTS]);

    const root = this.rootComponent();
    let first = true;
    start(
      (_: Context) => {
        if (this.ctx.bus.processQueue() || first) {
          first = false;
          return root;
        }
      },
      { root: this.config.domRoot, ctx: this.ctx }
    );
  }

  rootComponent(): any {
    const { ui } = this.ctx;
    return ['div', ui.app, root];
  }
}